#app {
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(4, 100px);
  grid-template-rows: repeat(6, 100px);
  grid-gap: 5px;
  grid-template-areas:
    "display display display display"
    "clear delete divide multiplication"
    "seven eight nine subtraction"
    "four five six addition"
    "one two three equals"
    "zeronum zeronum decimal equals";
}

#display {
  grid-area: display;
}

#clear {
  grid-area: clear;
}

#delete {
  grid-area: delete;
}

#divide {
  grid-area: divide;
}

#multiply {
  grid-area: multiplication;
}

#subtraction {
  grid-area: subtraction;
}

#addition {
  grid-area: addition;
}

#equals {
  grid-area: equals;
}

#one {
  grid-area: one;
}

#two {
  grid-area: two;
}

#three {
  grid-area: three;
}

#four {
  grid-area: four;
}

#five {
  grid-area: five;
}

#six {
  grid-area: six;
}

#seven {
  grid-area: seven;
}

#eight {
  grid-area: eight;
}

#nine {
  grid-area: nine;
}

#zero {
  grid-area: zeronum;
}

#app div {
  background-color: #8bdefe;
  font-family: "Arial", sans-serif;
  font-size: 1.5em;
  text-align: center;
  line-height: 100px;
}
